.to-do-list {
  font-family: Roboto;
  text-align: center;
  margin-top: 100px;
  width: 70vw; /* Ensure the list doesn't exceed the viewport */
  padding: 5px 20px; /* Add padding to keep the content away from the edges */
  margin: 0 auto;
  margin-left: -207px;
  background-color: red;

}

ol {
  padding: 0;
  list-style: none; /* Remove the default list styling */
}

li {
  background-color: #191919;
  padding: 15px;
  margin-bottom: 10px;
  margin-left: -17px;
  border: 2px solid rgb(38, 38, 38);
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%; /* Make the list item take full width of its container */
  max-width: 100%; /* Ensure it does not exceed the viewport */
}

.text {
  flex: 1;
  word-wrap: break-word; /* Allow long words to break */
  word-break: break-word; /* Avoid text spillover */
  overflow-wrap: break-word;
  overflow: hidden;
  padding-right: 10px;
}

input[type="text"] {
  font-family: Roboto;
  font-size: 16px;
  padding: 6px;
  width: 300px;
  border: 2px solid #ddd;
  border-radius: 6px;
  box-shadow: none;
  outline: none;
  transition: border-color 0.3s, box-shadow 0.3s;
  margin-right: 4px;
}
input[type="text"]:focus {
  border-color: #007bff;
  box-shadow: 0 0 8px rgba(0, 123, 255, 0.3);
}

button {
  padding-top: 9px;
  padding-left: 12px;
  padding-right: 12px;
  padding-bottom: 9px;
  border-radius: 5px;
  background-color: #232323;
  border: none;
  margin: 3px;
  font-family: Roboto;
  font-size: 18px;
  font-weight: 400;
  color: rgb(255, 255, 255);
}
button:hover {
  cursor: pointer;
  background-color: #363636;
}

body {
  background-color: #131313;
  color: white;
  text-align: center;
  font-family: Roboto;
}

body, html {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}


/* Toggle Button */
.nav-toggle-btn {
  position: fixed; /* Change to fixed */
  top: 3px;
  left: 3px; /* Adjust the position based on nav bar width */
  z-index: 2000; /* Ensure button is above all other content */
  background-color: #333;
  color: white;
  border: none;
  padding: 10px 15px;
  cursor: pointer;
  border-radius: 5px;
  transition: left 0.3s ease;
  background-color: rgba(0, 0, 0, 0);
  opacity: 50%;
  transition: opacity 0.2s ease-in-out; /* Duration and timing function */
}

.nav-toggle-btn:hover {
  background-color: rgba(0, 0, 0, 0);
  opacity: 100%;
}

.nav.collapsed ~ .nav-toggle-btn {
  left: 20px; /* Move button to the left when nav is collapsed */
}

/* Style the navigation bar */
.nav {
  width: 149px; /* Default width */
  height: 100vh; /* Full height of the viewport */
  position: fixed; /* Change to fixed */
  top: 0;
  left: 0;
  background-color: #1b1b1b; /* Background color */
  display: flex;
  align-items: start;
  justify-content: center;
  transition: width 0.3s ease; /* Smooth transition for width */
  z-index: 1000; /* Ensure it is above other content */
  overflow: hidden; /* Prevent scrollbars within the nav */
}

/* Collapsed state for nav */
.nav.collapsed {
  width: 60px; /* Collapsed width */
}

/* Style the unordered list inside the navigation */
.nav ul {
  list-style-type: none; /* Remove bullets */
  padding: 0;
  margin: 0;
}

/* Style each list item */
.nav ul li {
  height: 20px;
  background-color: rgba(255, 0, 0, 0);
  border: none;
  width: fit-content;
}

/* Style the links */
.nav ul li a {
  color: white;
  text-decoration: none;
  display: block;
  padding: 10px 10px;
  border-radius: 5px;
  text-align: center;
  font-size: 18px;
  background-color: rgba(64, 64, 64, 0); /* Transparent background */
  transition: background-color 0.17s ease-in-out;
  width: 100%;
  white-space: nowrap; /* Prevent text from wrapping */
  overflow: hidden; /* Hide overflow text */
  opacity: 85%;
  text-shadow: 0 0 0px rgba(0, 255, 255, 0.7); /* Glow effect */
  transition: text-shadow 0.2s ease-in-out;
}

/* Hide text in links when nav is collapsed */
.nav.collapsed ul li a {
  font-size: 0; /* Hide text */
}

/* Show icons only when collapsed */
.nav.collapsed ul li a::before {
  font-size: 24px; /* Display icon larger */
  font-size-adjust: inherit;
  margin-right: 0;
}

/* Style the links on hover */
.nav ul li a:hover {
  opacity: 100%;
  text-shadow: 0 0 7px rgb(78, 78, 78);
}

/* Adjust main content padding to avoid overlap with nav */
.main-content {
  padding: 20px;
  margin-left: 149px; /* Offset to account for nav width */
  transition: margin-left 0.3s ease; /* Smooth transition */
}

/* Expanded state for main content when nav is collapsed */
.main-content.expanded {
  margin-left: 60px; /* Adjust based on collapsed nav width */
}

/* React container adjustment */
.react-container {
  padding-left: 60px;
}

/* Dark overlay when nav is expanded */
.overlay {
  position: fixed; /* Change to fixed */
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.695); /* Semi-transparent dark background */
  z-index: 900; /* Ensure it's below the nav but above main content */
  display: none; /* Hidden by default */
  transition: opacity 0.3s ease;
}

/* Show overlay when nav is expanded */
.nav.collapsed ~ .overlay {
  display: none; /* Hide overlay when nav is collapsed */
}

.nav ~ .overlay {
  display: block; /* Show overlay when nav is expanded */
  opacity: 1;
}
